.homepage {
    display: grid;
    grid-template-rows: 15rem 1fr 1fr 1fr ;
    justify-items: center;
    grid-gap: 2rem;
    width: 100vw;
    height: 100vh;
}

.bodyclass {
    background-color: #1E2736;
    overflow: auto;
    padding: 0;
    margin: 0;
    font-family: 'vazir';
}

.home {
    max-width: 1200px;
    margin: 60px auto;
  }

.header {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    align-content: center;
    height: 12rem;
}
.contact {
    background-color: #E9DAD3;
    padding: 0.5%;
    padding-left: 30px;
    padding-right: 30px;
    border-radius: 15px;
    font-family: 'B Koodak';
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    grid-column: 3/3;
    grid-row: 1;
    align-self: center;
    justify-self: center;
    cursor: pointer;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
    height: 45px;
}

.contact:focus {
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.171);
    transform: translateY(-1rem);
    transition: all 0.5s;
}

.contact:hover {
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.171);
    transform: translateY(-1rem);
    transition: all 0.5s;

}

.profile {
    height: 80px;
    width: 80px;
    margin: 40px;
    /* top: -4px; */
    /* position: relative; */
    grid-column: 1;
    grid-row: 1;
    align-self: center;
    justify-self: center;
    /* left: -450px; */
    cursor: pointer;
    border-radius: 50%;
    border: 0.2rem solid #E9DAD3;
}

.profile:focus {
    transform: translateY(-1rem);
    transition: all 0.5s;
}

.profile:hover {
    transform: translateY(-1rem);
    transition: all 0.5s;

}

.profile_name {
    grid-row: 1;
    grid-column: 1;
    align-self: end;
    justify-self: center;
    font-size: 2rem;
    color: #E9DAD3;
    font-weight: 700;
}

.showplace {

    /* grid-row: 2 ; */
    background-color: #E9DAD3;
    width: 98%;
    border-radius: 15px;

    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;

    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;


    align-items: center;
    justify-content: center;
    align-content: space-between;
    padding: 5rem 0rem;
    height: 55rem;


}


.auction {
    grid-row: 3;
    background-color: #E9DAD3;
    width: 98%;
    height: 55rem;

    border-radius: 1.5rem;

    padding-top: 5rem;

    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    align-items: center;
    justify-content: center;
    align-content: space-between;
    height: 55rem;




}

